.container-box {
    border-radius: 5px;
    background-color:#fff;
    padding:60px 60px 0px;
    min-height:550px;
    overflow: hidden;
    /* position:absolute; */
}
.container-box>:first-child {
  margin-bottom:130px;
}
header {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width:100%;
  padding:0 40px;
  line-height: 60px;
  position:absolute;
  top:0px;
  z-index: 999;
  text-align:justify;
  text-align-last:justify;
}
.header i {
  z-index:2;
}
.box .layui-row{
  height:100%;
}
/*搜索*/
.search-box input {
  border-radius: 20px;
}
.reprint {
  margin-bottom:10px;
}
.footer {
  width: 100%;
  position:absolute;
  bottom:25px;
  left:0px;
  line-height: 22px;
  text-align: center;
}
/* 遮盖 */
.zhegai {
  width: 100%;
  height:100%;
  position: fixed;
  top:0px;
  left:0px;
  background: rgba(0,0,0,.2);
  z-index:5;
}

.user {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 210px;
  padding:20px 0px;
  background-image: url("/static/images/329964-150x300.jpg");
  text-align: center;
  font-size:14px;
  line-height:22px;
}
.user img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}
.user .username {
  margin-top:5px;
  color:#fff;
  font-weight: 700;
}
.user .signature{
  font-size:12px;
  color:hsla(0,0%,100%,.7);
}
.layui-nav .layui-nav-item a{
  padding-left:30px;
}
.aside-box {
  height:100%;
  position:fixed;
  top:0px;
  left:0px;
  z-index:999;
}
.aside-nav {
  position:absolute;
  top:210px;
  width:100%;
  height:100%;
}
.aside-nav ul{
  width:100%;
  height:100%;
}
/* 修改layui个别样式 */
.layui-nav-tree .layui-this>:first-child{
    background-color: #009688;
    color: #fff;
}
.layui-nav-tree .layui-this {
  background-color: transparent;
}
.layui-nav-tree .layui-cate-this {
    background-color: #009688;
    color: #fff;
}

.banner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background-image: url("/static/images/192642-15327772028c2b.jpg");
  background-position: center center;
  background-size: cover;
  z-index: 1;
}

.post {
    /* background-color: rgba(0,0,0,0.5); */
    /* background-color: rgb(247, 247, 247); */
    background-color: #fff;
    padding:10px 25px;
    border-radius: 3px;
    border:1px solid #ddd;
    border-top:none;
    /* box-shadow: 4px 4px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12); */
    margin-top:30px;
}
.list_cover {
    min-width: 95%;
    max-width: 100%;
    border:1px solid #a58d8d;
    border-radius: 2px;
}
.sketch{
    margin-bottom:0px;
    padding-bottom:5px;
    color:#5f5151;
    font-size:16px;
    line-height:1.8;
    /* background-color:rgb(247, 247, 247); */
    background-color: #fff;
}
.more {
    float:right;
    margin-right: 15px;
}
.more a{
    color: #01AAED;
}

.top-image {
    width:100%;
    height:100%;
}














